.home-container > img {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -1;
}

.home-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}
.home-contact {
    width: 100%;
    height: 5vh;
    background: #313958  ;
    color: white;
    position: fixed;
    align-self: start;
    alignment: top;
    z-index: 2;
    align-items: center;
    display: flex;
    padding-left: 20px;

}

.home-contact-row {
    display: flex;
    flex-direction: row;
}
.home-contact-row-logo-text {
    display: flex;
    flex-direction: row;
    margin-left: 20px;
}

.home-contact-row-logo-text >a {
    margin-left: 5px;
}

.home-container > a {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.home-container > p {
    margin-bottom: 70px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.home-btns {
    display: flex;
    justify-content: space-between;
    width: 500px;
    max-width: 100vw;
    padding: 3Vh;
}

